<template>
  <page
    @on-ready="onReady"
    :headerHeightVW="headerHeightVW"
    :scrollbar="true"
    ref="page"
    class="category-page"
  >
    <van-nav-bar slot="header" id="header" title="选择设备分类" :left-arrow="true" @click-left="onClickLeft">
    </van-nav-bar>

    <div class="list-container">
      <van-cell-group>
        <van-cell v-if="addressShow"><span style="color: rgba(0,0,0,0.5);" v-html="addressShow"></span></van-cell>
      </van-cell-group>

      
      <van-cell-group>
        <van-radio-group v-model="radio">
        <van-cell @click="handleAddress(item)" v-for="item in addressList" :key="item.id"  :is-link="!item.isLeaf" :clickable="!item.isLeaf">
          <!-- <van-button type="primary" size="mini" color="#1b82d2">选择</van-button> -->
          <van-radio slot="icon" :name="item.id" @click.stop="handleAddressSelect(item)" >&nbsp;&nbsp;</van-radio>
          <span>{{item.name}}</span>
        </van-cell>
        </van-radio-group>
      </van-cell-group>
    </div>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 0,
    addressList: [],
    address: [],
    id: null,
    mode: null,
    radio: null,
    profession: null
  }),
  computed: {
    addressShow () {
      let strArray = []
      if (this.address) {
        for (let i = 0; i < this.address.length; i++) {
          strArray.push(this.address[i].name)
        }
      }
      
      strArray[strArray.length - 1] = `<span style="color: #108EE9;">${strArray[strArray.length - 1]}</span>`
      return strArray.join(' > ')
    }
  },
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      if (navigate.getParam('address')) {
        this.address = navigate.getParam('address')
      }

      if (navigate.getParam('profession')) {
        this.profession = navigate.getParam('profession')
      }

      localStorage.setItem('category_select', '')

      this.loadList()
    },
    loadList () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      let parent = 0

      if (this.address.length) {
        parent = this.address[this.address.length - 1].id
      }

      db.getDeviceCategory(parent).then(data => {
        this.addressList = data.filter(item => parent ? true : item.profession == this.profession)
        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    handleAddress (item) {
      if (item.isLeaf) {
        return false
      }

      let addressParam = [...this.address]
      addressParam.push(item)

      let cid = []

      if (navigate.getParam('cid')) {
        cid = navigate.getParam('cid')
      }

      cid.push(item.id)

      navigate.copen('deviceCategory', item.id, {address: addressParam, radom: (new Date()).getTime(), cid, profession: this.profession })
    },

    handleAddressSelect (item) {
      this.radio = item.id

      let addressParam = [...this.address]
      addressParam.push(item)

      localStorage.setItem('category_select', JSON.stringify(addressParam))

      navigate.back('deviceCategory')

      if (navigate.getParam('cid')) {
        const cid = navigate.getParam('cid')

        for (let i = 0; i < cid.length; i++) {
          navigate.back('deviceCategory_'+cid[i])
        }
      }
    },

    onClickLeft () {
      navigate.back()
    }
  }
}
</script>

<style lang="scss">
.list-container{
  .van-cell-group+.van-cell-group {
    margin-top: 12px;
  }

  .user-icon{
    width: 45px;
    height: 45px;
    margin-right: 12px;
    border-radius: 50%;
  }
}
</style>
